.searchContainer {
  display: flex;
  justify-content: center;
}

.searchBox {
  position: relative;
}

.searchButton {
  position: absolute;
  right: 8px;
  top: 0;
  height: 100%;
}

.searchInput {
  font-size: medium;
  height: 30px;
  border-radius: 5px;
  padding: 5px 30px 5px 10px;
  border: 1px solid white;
}

.searchInput:focus,
.searchButton:focus {
  outline: none;
  border: 1px solid ;
  box-shadow: 0 0 5px;
}

@media (min-width: 760px) {
  .searchInput{
    font-size: large;
  }
}